.chatContainer {
  border: 1px solid var(--color-400);
  border-radius: 20px;
  box-shadow: 0 5px 10px 2px var(--color-400);
  max-width: 800px;
  height: 100vh;
  margin: 0 auto;
  background-color: var(--color-100);

  .chatHeader {
    max-height: 120px;
    border-bottom: 1px solid var(--color-400);
    padding-left: 80px;
    display: flex;
    align-items: center;
    gap: 20px;

    img {
      width: 90px;
      height: 90px;
      border-radius: 50%;
      object-fit: cover;
    }

    div {
      font-size: var(--font-size-xl);
      font-weight: var(--font-weight-bold);
    }
  }

  .messagesContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 40px;

    .messages {
      padding: 20px;
      height: 500px;
      width: 100%;
      overflow-y: scroll;

      .messageWrapper {
        display: flex;
        align-items: baseline;
      }

      .message {
        border-radius: 10px;
        max-width: 100%;
        height: 48px;
        margin: 10px;
        padding: 12px 20px 10px 20px;
        font-size: var(--font-size-l);
        background: linear-gradient(15deg, var(--color-700) 0%, var(--color-800) 100%);
        color: var(--color-100);

        b {
          color: var(--color-600);
        }
      }
    }

    .inputAndTextareaContainer {
      margin-top: 5px;
      width: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      flex-direction: column;

      button {
        cursor: pointer;
        width: 60px;
        height: 30px;
        border: none;
        border-radius: 10px;
        background-color: var(--color-700);
        box-shadow: 0 5px 10px 2px var(--color-400);
        color: var(--color-100);
        font-size: var(--font-size-s);

        &:disabled {
          background-color: var(--color-500);
        }
      }

      .inputWrapper {
        input {
          border: 1px solid var(--color-500);
          border-radius: 5px;
          padding: 5px;

          &:disabled {
            color: var(--color-500);
          }
        }
      }

      .textareaWrapper {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: baseline;

        textarea {
          margin-top: 5px;
          width: 50%;
          resize: none;
          border: 1px solid var(--color-700);
          border-radius: 10px;
          padding: 10px;
        }
      }
    }
  }

  .error {
    color: var(--color-900);
    font-size: var(--font-size-s);
  }
}

//scrollbar styles:
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(var(--color-800) 0%, var(--color-700) 100%);
  border-radius: 5px;
}
::-webkit-scrollbar-track {
  background-color: var(--color-350);
  border-radius: 5px;
}
::-webkit-scrollbar-button {
  display: none;
}

